import React from 'react'
import { Media, Row, Col, Button } from 'reactstrap'
import { useSelector } from 'react-redux'
import * as Yup from 'yup'
import { FormattedMessage } from 'react-intl'
import { Formik, Form } from 'formik'
import BaseFormGroup from '../../components/BaseFormGroup'
import BaseFormDatePicker from '../../components/BaseFormDatePicker'
import { GENDER_OPTIONS } from '../../configs/app-configs'
import BaseFormGroupSelect from '../../components/BaseFormGroupSelect'

const validationSchema = Yup.object().shape({
  icType: Yup.string().required(
    <FormattedMessage id='completeInformation.nbrPer.required' />
  ),
  icNumber: Yup.string()
    .required(<FormattedMessage id='completeInformation.nbrPer.required' />)
    .when('icType', {
      is: 'CMND',
      then: Yup.string().matches(/^(\d{9}|\d{12})$/, () => (
        <FormattedMessage id='completeInformation.nbrPer.invalid' />
      ))
    })
    .when('icType', {
      is: 'CCCD',
      then: Yup.string().matches(/^(\d{12})$/, () => (
        <FormattedMessage id='completeInformation.nbrPer.invalid' />
      ))
    })
    .when('icType', {
      is: 'HC',
      then: Yup.string().matches(/^(?!^0+$)[a-zA-Z0-9]{3,20}$/, () => (
        <FormattedMessage id='completeInformation.nbrPer.invalid' />
      ))
    }),
  dateOfBirth: Yup.string().required(
    <FormattedMessage id='completeInformation.dateOfBirth.required' />
  ),
  address: Yup.string().required(
    <FormattedMessage id='completeInformation.address.required' />
  ),
  bankName: Yup.string().required(
    <FormattedMessage id='completeInformation.bank.required' />
  ),
  bankBranch: Yup.string().required(
    <FormattedMessage id='completeInformation.branch.required' />
  ),
  bankNumber: Yup.string().required(
    <FormattedMessage id='completeInformation.accountNbr.required' />
  ),
  city: Yup.string().required(
    <FormattedMessage id='completeInformation.city.required' />
  ),
  ward: Yup.string().required(
    <FormattedMessage id='completeInformation.ward.required' />
  ),
  district: Yup.string().required(
    <FormattedMessage id='completeInformation.district.required' />
  )
})

const bank = [
  { value: '1', label: 'Tien Phong Bank' },
  { value: '2', label: 'Vietcombank' },
  { value: '3', label: 'BIDV' }
]

const city = [
  { value: 'HN', label: 'Hà Nội' },
  { value: 'TPHCM', label: 'Thành phố HCM' },
  { value: 'DN', label: 'Đà nẵng' }
]

const district = [
  { value: 'HN', label: 'Nam Từ Liêm' },
  { value: 'TPHCM', label: 'Thành phố HCM' },
  { value: 'DN', label: 'Đà nẵng' }
]

const wards = [
  { value: 'HN', label: 'Phạm Hùng' },
  { value: 'TPHCM', label: 'Lưu Hữu Phước' },
  { value: 'DN', label: 'Mễ Trì' }
]

const UserAccountTab = () => {
  let { userDetails, userSettings, ...user } = useSelector(
    (state) => state.auth.user
  )

  userDetails = userDetails || {}
  userSettings = userSettings || {}
  return (
    <Row>
      <Col sm='12'>
        <Media className='mb-2'>
          <Media className='mr-2 my-25' left href='#'>
            <Media
              className='users-avatar-shadow rounded'
              object
              src={userSettings ? userSettings.avatar : ''}
              alt='user profile image'
              height='84'
              width='84'
            />
          </Media>
          <Media className='mt-2' body>
            <Media className='font-medium-1 text-bold-600' tag='p' heading>
              {user.fullName}
            </Media>
            <div>Mã tài khoản : {user.userCode}</div>
            <div className='d-flex flex-wrap'>
              <Button.Ripple className='mr-1 mt-2' color='primary' outline>
                Change
              </Button.Ripple>
              <Button.Ripple  className='mt-2' color='danger' outline>Remove Avatar</Button.Ripple>
            </div>
          </Media>
        </Media>
      </Col>
      <Col sm='12'>
        <Formik
          enableReinitialize
          validationSchema={validationSchema}
          initialValues={{
            fullName: user.fullName || '',
            icNumber: user.icNumber || '',
            dateOfBirth: user.dateOfBirth || '',
            gender: user.gender || 'MALE',
            city: userDetails.city || '',
            district: userDetails.district || '',
            ward: userDetails.ward || '',
            address: userDetails.address || '',
            bankName: userDetails.bankName || '',
            bankBranch: userDetails.bankBranch || '',
            bankNumber: userDetails.bankNumber || ''
          }}
        >
          {({ errors, touched }) => (
            <Form>
              <Row className='mt-2'>
                <Col sm='12' md='6'>
                  <BaseFormGroup
                    messageId='resgister.fullName'
                    fieldName='fullName'
                    errors={errors}
                    touched={touched}
                  ></BaseFormGroup>
                </Col>
                <Col sm='12' md='6'>
                  <BaseFormGroup
                    messageId='completeInformation.nbrPer'
                    fieldName='icNumber'
                    errors={errors}
                    touched={touched}
                  ></BaseFormGroup>
                </Col>
              </Row>
              <Row className='mt-2'>
                <Col sm='12' md='6'>
                  <BaseFormDatePicker
                    messageId='completeInformation.dateOfBirth'
                    fieldName='dateOfBirth'
                    errors={errors}
                    touched={touched}
                  ></BaseFormDatePicker>
                </Col>
                <Col sm='12' md='6'>
                  <BaseFormGroupSelect
                    messageId='completeInformation.gender'
                    fieldName='gender'
                    defaultValue={GENDER_OPTIONS[0]}
                    options={GENDER_OPTIONS}
                    errors={errors}
                    touched={touched}
                  />
                </Col>
              </Row>
              <Row className='mt-2'>
                <Col sm='12' md='6'>
                  <BaseFormGroup
                    messageId='register.phoneNumber'
                    fieldName='phoneNumber'
                    errors={errors}
                    touched={touched}
                  ></BaseFormGroup>
                </Col>
                <Col sm='12' md='6'>
                  <BaseFormGroup
                    messageId='register.email'
                    fieldName='email'
                    errors={errors}
                    touched={touched}
                  />
                </Col>
              </Row>
              <Row className='mt-2'>
                <Col sm='12'>
                  <BaseFormGroup
                    messageId='completeInformation.address'
                    fieldName='address'
                    errors={errors}
                    touched={touched}
                  />
                </Col>
              </Row>
              <Row className='mt-2'>
                <Col sm='4'>
                  <BaseFormGroupSelect
                    messageId='completeInformation.province'
                    fieldName='city'
                    options={city}
                    errors={errors}
                    touched={touched}
                  />
                </Col>
                <Col sm='4'>
                  <BaseFormGroupSelect
                    messageId='completeInformation.district'
                    fieldName='district'
                    options={district}
                    errors={errors}
                    touched={touched}
                  />
                </Col>
                <Col sm='4'>
                  <BaseFormGroupSelect
                    messageId='completeInformation.ward'
                    fieldName='ward'
                    options={wards}
                    errors={errors}
                    touched={touched}
                  />
                </Col>
              </Row>
              <Row className='mt-2'>
                <Col sm='4'>
                  <BaseFormGroupSelect
                    messageId='completeInformation.bank'
                    fieldName='bankName'
                    options={bank}
                    errors={errors}
                    touched={touched}
                  />
                </Col>
                <Col sm='4'>
                  <BaseFormGroup
                    messageId='completeInformation.branch'
                    fieldName='bankBranch'
                    errors={errors}
                    touched={touched}
                  />
                </Col>
                <Col sm='4'>
                  <BaseFormGroup
                    messageId='completeInformation.accountNbr'
                    fieldName='bankNumber'
                    errors={errors}
                    touched={touched}
                  />
                </Col>
              </Row>
              <Col
                className='d-flex justify-content-end flex-wrap mt-2'
                sm='12'
              >
                <Button.Ripple type='button' color='secondary'>
                  Trang chủ
                </Button.Ripple>
                <Button.Ripple className='ml-3' type='submit' color='primary'>
                  Lưu thay đổi
                </Button.Ripple>
              </Col>
            </Form>
          )}
        </Formik>
        <Row></Row>
      </Col>
    </Row>
  )
}
export default UserAccountTab
