import React from 'react'
import { FormattedMessage, injectIntl } from 'react-intl'
import { Form, Formik } from 'formik'
import '../../assets/scss/pages/complete-infor.scss'
import { Row, Col, Button, Label } from 'reactstrap'
import * as Yup from 'yup'
import BaseFormGroup from '../../components/BaseFormGroup.js'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import BaseFormGroupSelect from '../../components/BaseFormGroupSelect'
import BaseFormDatePicker from '../../components/BaseFormDatePicker'
import { compeleteInfo } from '../../redux/actions/auth'
import { GENDER_OPTIONS } from '../../configs/app-configs'

const CompleteInforValidate = Yup.object().shape({
  icType: Yup.string().required(
    <FormattedMessage id='completeInformation.nbrPer.required' />
  ),
  icNumber: Yup.string().required(
    <FormattedMessage id='completeInformation.nbrPer.required' />
  ).when('icType', {
    is : 'CMND',
    then : Yup.string().matches(/^(\d{9}|\d{12})$/, () => <FormattedMessage id='completeInformation.nbrPer.invalid' />)
  }).when('icType', {
    is : 'CCCD',
    then : Yup.string().matches(/^(\d{12})$/, () => <FormattedMessage id='completeInformation.nbrPer.invalid' />)
  }).when('icType', {
    is : 'HC',
    then : Yup.string().matches(/^(?!^0+$)[a-zA-Z0-9]{3,20}$/, () => <FormattedMessage id='completeInformation.nbrPer.invalid' />)
  }),
  dateOfBirth: Yup.string().required(
    <FormattedMessage id='completeInformation.dateOfBirth.required' />
  ),
  address: Yup.string().required(
    <FormattedMessage id='completeInformation.address.required' />
  ),
  bankName: Yup.string().required(
    <FormattedMessage id='completeInformation.bank.required' />
  ),
  bankBranch: Yup.string().required(
    <FormattedMessage id='completeInformation.branch.required' />
  ),
  bankNumber: Yup.string().required(
    <FormattedMessage id='completeInformation.accountNbr.required' />
  ),
  city: Yup.string().required(
    <FormattedMessage id='completeInformation.city.required' />
  ),
  ward: Yup.string().required(
    <FormattedMessage id='completeInformation.ward.required' />
  ),
  district: Yup.string().required(
    <FormattedMessage id='completeInformation.district.required' />
  )
})

const personalInfoOptions = [
  { value: 'HC', label: 'Hộ chiếu', color: '#338955', isFixed: true },
  {
    value: 'CMND',
    label: 'Chứng minh nhân dân',
    color: '#338955',
    isFixed: true
  },
  {
    value: 'CCCD',
    label: 'Căn cước công dân',
    color: '#338955',
    isFixed: true
  }
]

const bank = [
  { value: '1', label: 'Tien Phong Bank' },
  { value: '2', label: 'Vietcombank' },
  { value: '3', label: 'BIDV' }
]

const city = [
  { value: 'HN', label: 'Hà Nội' },
  { value: 'TPHCM', label: 'Thành phố HCM' },
  { value: 'DN', label: 'Đà nẵng' }
]

const district = [
  { value: 'HN', label: 'Nam Từ Liêm' },
  { value: 'TPHCM', label: 'Thành phố HCM' },
  { value: 'DN', label: 'Đà nẵng' }
]

const wards = [
  { value: 'HN', label: 'Phạm Hùng' },
  { value: 'TPHCM', label: 'Lưu Hữu Phước' },
  { value: 'DN', label: 'Mễ Trì' }
]

const CompleteInformation = ({ intl }) => {
  const user = useSelector((state) => state.auth.register.user)
  const dispatch = useDispatch()

  const onSubmit = (values) => {
    dispatch(compeleteInfo(trimObjectValues(values)))
  }

  return (
    <div className='completeInfor'>
      <Formik
        initialValues={{
          icType: '',
          icNumber: '',
          dateOfBirth: '',
          gender: 'MALE',
          city: '',
          district: '',
          ward: '',
          address: '',
          refCode: '',
          bankName: '',
          bankBranch: '',
          bankNumber: ''
        }}
        validationSchema={CompleteInforValidate}
        onSubmit={onSubmit}
      >
        {({ errors, touched }) => (
          <Form>
            <Row>
              {/* form left */}
              <Col sm='12' lg='3' className='mb-3'>
                <Row className='ml-2'>
                  <Label className='font-weight-bold'>
                    <FormattedMessage id='register.fullname' />
                  </Label>
                </Row>
                <Row className='ml-3'>
                  <span className='text-gray'>{user.fullName}</span>
                </Row>
                <Row className='ml-2  mt-2'>
                  <Label className='font-weight-bold'>
                    <FormattedMessage id='register.phoneNumber' />
                  </Label>
                </Row>
                <Row className='ml-3'>
                  <span className='text-gray'>{user.phoneNumber}</span>
                </Row>
                <Row className='ml-2  mt-2'>
                  <Label className='font-weight-bold'>Email*</Label>
                </Row>
                <Row className='ml-3'>
                  <span className='text-gray'>{user.email}</span>
                </Row>
              </Col>

              {/* form right */}
              <Col sm='12' lg='9'>
                <Row>
                  <Col sm='6'>
                    <BaseFormGroupSelect
                      messageId='completeInformation.idType'
                      fieldName='icType'
                      options={personalInfoOptions}
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='6'>
                    <BaseFormGroup
                      messageId='completeInformation.nbrPer'
                      fieldName='icNumber'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col sm='6'>
                    <BaseFormDatePicker
                      messageId='completeInformation.dateOfBirth'
                      fieldName='dateOfBirth'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='6'>
                    <BaseFormGroupSelect
                      messageId='completeInformation.gender'
                      fieldName='gender'
                      defaultValue={GENDER_OPTIONS[0]}
                      options={GENDER_OPTIONS}
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col sm='4'>
                    <BaseFormGroupSelect
                      messageId='completeInformation.province'
                      fieldName='city'
                      options={city}
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroupSelect
                      messageId='completeInformation.district'
                      fieldName='district'
                      options={district}
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroupSelect
                      messageId='completeInformation.ward'
                      fieldName='ward'
                      options={wards}
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col sm='8'>
                    <BaseFormGroup
                      messageId='completeInformation.address'
                      fieldName='address'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroup
                      messageId='completeInformation.gif'
                      fieldName='refCode'
                      isRequired={false}
                    />
                  </Col>
                </Row>

                <Row>
                  <Col sm='4'>
                    <BaseFormGroupSelect
                      messageId='completeInformation.bank'
                      fieldName='bankName'
                      options={bank}
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroup
                      messageId='completeInformation.branch'
                      fieldName='bankBranch'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroup
                      messageId='completeInformation.accountNbr'
                      fieldName='bankNumber'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>
                <div className='d-flex justify-content-center justify-content-md-end'>
                  <Link to='/create-password'>
                    <Button.Ripple type='button'>
                      <FormattedMessage id='completeInformation.back' />
                    </Button.Ripple>
                  </Link>
                  <Button.Ripple type='submit' className='ml-2'>
                    <FormattedMessage id='completeInformation.done' />
                  </Button.Ripple>
                </div>
              </Col>
            </Row>
          </Form>
        )}
      </Formik>
    </div>
  )
}
export default injectIntl(CompleteInformation)
