import Axios from 'axios'
import React from 'react'
import { AlertTriangle } from 'react-feather'
import { toast } from 'react-toastify'
import { generateUUID } from '.'
import { API_BASE_URL } from '../configs/app-configs'
import { HIDE_LOADING_BAR, SHOW_LOADING_BAR } from '../redux/actions/ui/Index'

const HttpClient = Axios.create({
  timeout: 10000,
  // adapter: throttleAdapterEnhancer(
  //   cacheAdapterEnhancer(Axios.defaults.adapter, { threshold: 15 * 60 * 1000 })
  // ),
  // invalidate: async (config, request) => {
  //   if (request.clearCacheEntry) {
  //     await config.store.removeItem(config.uuid)
  //   }
  // }
})

HttpClient.defaults.headers['Content-Type'] = 'application/json'

export const errorMessage = (message) => {
  return (
    <div className='d-flex align-items-center'>
      <AlertTriangle /> <span className='ml-1'>{message}</span>
    </div>
  )
}

export const setUpHttpClient = (store, apiBaseUrl) => {
  let deviceId = localStorage.getItem('deviceId')
  let language = localStorage.getItem('language')
  if (!deviceId) {
    deviceId = generateUUID()
    localStorage.setItem('deviceId', deviceId)
  }
  if (!language) {
    localStorage.setItem('language', 'vi')
  }

  HttpClient.defaults.baseURL = apiBaseUrl || API_BASE_URL

  HttpClient.interceptors.request.use((config) => {
    const token = store.getState().auth.authToken
    language = localStorage.getItem('language')
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }
    config.headers.deviceId = deviceId
    config.headers.language = language
    const requestUUID = generateUUID();
    config.uuid = requestUUID;
    if (!config.isBackgroundRequest) {
      store.dispatch({ type: SHOW_LOADING_BAR, payload : requestUUID })
    }
    return config
  })

  HttpClient.interceptors.response.use(
    (response) => {
      store.dispatch({ type: HIDE_LOADING_BAR , payload : response.config.uuid})
      return response
    },
    (e) => {
      store.dispatch({ type: HIDE_LOADING_BAR , payload : e.config.uuid})
      if (!e.response) {
        return e
      }
      switch (e.response.status) {
        case 403:
          toast.error(errorMessage(e.response.data.message))
          store.dispatch({ type: 'LOGOUT_ACTION' })
        case 400:
        case 500:
          toast.error(errorMessage(e.response.data.message))
      }
      return e.response
    }
  )
}

export default HttpClient
