import React, { useRef, useState } from 'react'
import ReactSelect from 'react-select'
import { FormGroup, Label } from 'reactstrap'
import classnames from 'classnames'

const Select = (props) => {
  const [inputValue, setInputValue] = useState(props.defaultValue || '')
  const [isFocused, setIsFocused] = useState(false)

  const onChange = (e, actions) => {
    if (props.onChange) {
      props.onChange(e, actions)
    }
    if (props.isMulti) {
      setInputValue(e ? e.map((item) => item.value).join() : '')
    } else {
      setInputValue(e ? e.value : '')
    }
  }

  const onFocus = (e) => {
    if (props.onFocus) {
      props.onChange(e)
    }
    setIsFocused(true)
  }

  const onBlur = (e) => {
    if (props.onBlur) {
      props.onBlur(e)
    }
    setIsFocused(false)
  }

  return (
    <FormGroup className='form-label-group position-relative'>
      <ReactSelect
        {...props}
        onChange={onChange}
        onBlur={onBlur}
        onFocus={onFocus}
        theme={(theme) => ({
          ...theme,
          colors: {
            ...theme.colors,
            primary: '#338955'
          }
        })}
      />
      {props.required ? (
        props.errors[props.fieldName] && props.touched[props.fieldName] ? (
          <div
            className='text-danger'
            // style={{ position: 'absolute', top: '2.7rem' }}
          >
            {props.errors[props.fieldName]}
          </div>
        ) : null
      ) : (
        ''
      )}
      <input
        className='d-none'
        placeholder={props.placeholder}
        value={inputValue}
      />
      <Label className={classnames({ 'text-primary': isFocused })}>
        {props.placeholder}
      </Label>
    </FormGroup>
  )
}

export default Select
