import { FastField } from 'formik'
import React from 'react'
import { injectIntl } from 'react-intl'
import { FormGroup } from 'reactstrap'
import Select from './Select'

const BaseFormGroupSelect = ({
  fieldName,
  errors,
  touched,
  messageId,
  options,
  intl,
  defaultValue,
  isRequired = true
}) => {
  return (
    <FastField name='fieldName'>
      {({ field, form }) => (
        <Select
          placeholder={intl.formatMessage({
            id: messageId
          })}
          className={`${
            isRequired &&
            errors[fieldName] &&
            touched[fieldName] &&
            'is-invalid'
          }`}
          classNamePrefix='Select'
          fieldName={fieldName}
          required={isRequired}
          defaultValue={defaultValue}
          errors={errors}
          touched={touched}
          options={options}
          onChange={(e) => {
            form.setFieldValue(fieldName, e.value)
          }}
        />
      )}
    </FastField>
  )
}

export default injectIntl(BaseFormGroupSelect)
