import React, { useState } from 'react'
import { Check } from 'react-feather'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label, Button } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import CheckBox from '../../components/Checkbox'
import { Formik, Field, Form } from 'formik'
import * as Yup from 'yup'
import { AuthService } from '../../services/authService'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'
import BaseFormGroup from '../../components/BaseFormGroup.js'
import { PHONE_REGEX } from '../../configs/app-configs'

const formSchema = Yup.object().shape({
  fullName: Yup.string().required(
    <FormattedMessage id='register.fullname.required' />
  ),
  email: Yup.string()
    .required(<FormattedMessage id='register.email.required' />)
    .email(<FormattedMessage id='register.email.invalid' />),
  phoneNumber: Yup.string()
    .matches(PHONE_REGEX, () => (
      <FormattedMessage id='register.phoneNumber.invalid' />
    ))
    .required(<FormattedMessage id='register.phoneNumber.required' />),
  refCode: Yup.string()
    .length(10, () => <FormattedMessage id='register.referalCode.invalid' />)
    .matches(PHONE_REGEX, () => (
      <FormattedMessage id='register.referalCode.invalid' />
    ))
})

const Register = () => {
  const [isAppcepted, setIsAppcepted] = useState(false)
  const [isNotApccepted, setIsNotAccepted] = useState(false)
  const history = useHistory()

  const onSubmit = async (values) => {
    if (!isAppcepted) {
      setIsNotAccepted(true)
      return
    }
    const res = await AuthService.register(trimObjectValues(values))
    if (res.status === 200 && res.data) {
      toast.success(<FormattedMessage id='register.registerSuccess' />)
      history.push('/login')
    }
  }

  const ontoggleAccepted = (checked) => {
    setIsAppcepted(checked)
    setIsNotAccepted(!checked)
  }

  const renderForm = ({ errors, touched }) => (
    <Form>
      <BaseFormGroup
        fieldName='fullName'
        errors={errors}
        touched={touched}
        messageId='register.fullname'
      />
      <FormGroup className='form-label-group position-relative'>
        <Field
          name='email'
          className={`form-control ${
            errors.email && touched.email && 'is-invalid'
          }`}
          placeholder='Email *'
        />
        {errors.email && touched.email ? (
          <div className='text-danger'>{errors.email}</div>
        ) : null}
        <Label>Email *</Label>
      </FormGroup>
      <BaseFormGroup
        fieldName='phoneNumber'
        errors={errors}
        touched={touched}
        messageId='register.phoneNumber'
      />
      <BaseFormGroup
        fieldName='refCode'
        errors={errors}
        touched={touched}
        messageId='register.refCode'
      />
      <FormGroup>
        <div className='d-flex align-items-center'>
          <CheckBox
            color='primary'
            icon={<Check className='vx-icon' size={16} />}
            onChange={(e) => ontoggleAccepted(e.target.checked)}
            defaultChecked={isAppcepted}
          />
          <div>
            <FormattedMessage id='register.agreeWith' />{' '}
            <a className='text-primary'>
              <FormattedMessage id='register.policyAndCondition' />
            </a>{' '}
            <FormattedMessage id='register.useService' />
          </div>
        </div>
        {isNotApccepted ? (
          <div className='text-danger'>
            <FormattedMessage id='register.mustAppcepted' />
          </div>
        ) : null}
        <div className='d-flex justify-content-center mt-2'>
          <Button color='primary' type='submit'>
            <FormattedMessage id='register' />
          </Button>
        </div>
      </FormGroup>
    </Form>
  )

  return (
    <Formik
      initialValues={{
        fullName: '',
        email: '',
        phoneNumber: '',
        refCode: ''
      }}
      render={renderForm}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    ></Formik>
  )
}

export default Register
